<template>
  <div id="chat-container">
    <div v-for="(q, index) in question" :index="index" :key="q.text" class="chat">
      <div class="chat-message" v-bind:class="{ myMsg: !q.botMsg, botMsg: q.botMsg }">{{q.text}}</div>
      <div v-if="q.options">
        <div
          v-show="!q.answered"
          class="chat-options"
          v-for="(r, idx) in q.options"
          :index="idx"
          :key="r.name"
          align="center"
        >
          <div v-show="!q.answered" @click="selected(index, r)">{{r}}</div>
        </div>
      </div>
      <div class="bubble-pointer">
        <div class="empty-triangle"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      question: [
        {
          id: 0,
          text: "請問您是原告被告？",
          botMsg: true,
          answered: false,
          options: ["原告", "被告"]
        }
      ],
      idx: 0
    };
  },
  computed: {},
  methods: {
    getIdx(index) {
      let x = this.question.find(item => item.id == index);
      return x;
    },
    // getName(index) {
    //   // let x = this.question.find(item => item.id == index);
    //   // return x.choise;
    // },
    selected: function(index, choise) {
      console.log(index);
      this.question[index].answered = !this.question[index].answered;

      this.idx++;
      this.question.push({
        id: index + 1,
        text: choise,
        botMsg: false,
        selections: { name: "", options: [] }
      });

      this.pushMsg(choise, this.idx);
    },
    pushMsg: function(choise, index) {
      let q = {
        id: index,
        text: "",
        botMsg: true,
        answered: false,
        options: []
      };

      if (choise === "原告" || choise === "被告") {
        q.text = "請問您是哪種專利？";
        q.options = ["實體", "軟體", "創作專利"];
      }

      if (choise === "創作專利") {
        q.text = "是否屬特殊創作";
        q.options = ["是特殊創作", "不是特殊創作"];
      }

      if (choise === "是特殊創作") {
        q.text = "外觀像不像";
        q.options = ["外觀類似", "外觀不同"];
      }

      if (choise === "不是特殊創作") {
        q.text = "創作外觀是否相似";
        q.options = ["創作外觀類似", "創作外觀不同"];
      }

      if (choise === "創作外觀類似") {
        q.text = "已侵權";
        q.options = ["重新判斷", "查看報告"];
      }

      if (choise === "創作外觀不同") {
        q.text =
          "不構成侵權創作\n如果有任何疑問，歡迎致電童先生\n電話：0978716657";
        q.options = ["重新判斷", "查看報告"];
      }

      if (choise === "外觀不同") {
        q.text = "不構成侵權創作";
      }

      if (choise === "外觀類似") {
        q.text = "已侵權是否間接故意侵權";
        q.options = ["有間接故意", "無間接故意"];
      }

      if (choise === "實體") {
        q.text = "構造/外觀 像不像？";
        q.options = ["構造外觀像", "構造外觀不像"];
      }

      if (choise === "軟體") {
        q.text = "端點/介面/單元";
        q.options = ["設計邏輯類似", "設計邏輯明顯不同"];
      }

      if (choise === "構造外觀像") {
        q.text = "是否可以輕易完成";
        q.options = ["構造外觀容易複製", "構造外觀不容易複製"];
      }

      if (choise === "構造外觀容易複製" || choise === "設計邏輯類似") {
        q.text = "無違法\n如果有任何疑問，歡迎致電童先生\n電話：0978716657";
        q.options = ["重新判斷", "查看報告"];
      }

      if (choise === "構造外觀不像" || choise === "設計邏輯明顯不同") {
        q.text = "是否具進步性";
        q.options = ["具進步性", "不具進步性"];
      }

      if (choise === "具進步性") {
        q.text = "兩個產品相像嗎";
        q.options = ["相像", "不相像"];
      }

      if (choise === "不具進步性") {
        q.text =
          "不構成專利權侵害\n如果有任何疑問，歡迎致電童先生\n電話：0978716657";
        q.options = ["重新判斷", "查看報告"];
      }

      if (choise === "相像") {
        q.text = "是否間接故意侵權";
        q.options = ["有間接故意", "無間接故意"];
      }

      if (choise === "不相像") {
        q.text = "未侵權\n如果有任何疑問，歡迎致電童先生\n電話：0978716657";
        q.options = ["重新判斷", "查看報告"];
      }

      if (choise === "無間接故意") {
        q.text = "未侵權\n如果有任何疑問，歡迎致電童先生\n電話：0978716657";
        q.options = ["重新判斷", "查看報告"];
      }

      if (choise === "有間接故意") {
        q.text =
          "有侵權，計算結果\n如果有任何疑問，歡迎致電童先生\n電話：0978716657";
        q.options = ["重新判斷", "查看報告"];
      }

      if (choise === "重新判斷") {
        q.text = "載入中....";
        window.location.reload();
      }

      if (choise === "查看報告") {
        q.text = "計算中...";
        this.$router.push("summary");
      }

      this.question.push(q);
    }
  }
};
</script>

