<template>
  <div>
    <p>本網頁數據僅供參考，不負擔任一切法律責任</p>
    <Grid :data-items="summary" :columns="columns" :skip="skip" :take="take"></Grid>

    <kendo-chart
      :title-text="'次數比較直方圖'"
      :legend-position="'bottom'"
      :series="series"
      :category-axis-categories="categories"
      :value-axis="axis"
      :theme="'sass'"
    ></kendo-chart>
  </div>
</template>

<script>
import dsummary from "../../api/data.js";
import { process } from "@progress/kendo-data-query";

export default {
  data() {
    return {
      summary: [],
      series: [
        {
          name: "勝訴",
          data: [4, 4, 6, 3, 9, 2, 6, 3, 2, 2, 3, 0],
          axis: "freq"
        },
        {
          name: "勝敗互見",
          data: [5, 12, 21, 15, 18, 26, 11, 15, 9, 6, 6, 3],
          axis: "freq"
        },
        {
          name: "敗訴",
          data: [54, 139, 179, 133, 138, 136, 98, 95, 86, 97, 90, 44],
          axis: "freq"
        }
      ],
      categories: [97, 98, 99, 100, 101, 102, 103, 104, 105, 106, 107, 108],
      axis: [
        {
          name: "freq",
          labels: {
            format: "{0}"
          }
        }
      ],
      skip: 0,
      take: 20,
      columns: [
        { field: "case_year" },
        { field: "win" },
        { field: "win_amt" },
        { field: "draw" },
        { field: "draw_amt" },
        { field: "lose" },
        { field: "lose_amt" }
      ]
    };
  },
  created() {
    this.summary = process(dsummary, {
      skip: this.skip,
      take: this.take,
      sort: this.sort,
      group: this.group
    });
  },
  methods: {
    createAppState: function(dataState) {
      this.take = dataState.take;
      this.skip = dataState.skip;
      if (dataState.group) {
        dataState.group.map(group => (group.aggregates = this.aggregates));
      }
      this.group = dataState.group;
      this.filter = dataState.filter;
      this.sort = dataState.sort;
    },
    dataStateChange(event) {
      this.createAppState(event.data);
      this.dataResult = process(dsummary, {
        skip: this.skip,
        take: this.take,
        sort: this.sort,
        filter: this.filter,
        group: this.group
      });
    }
  }
};
</script>

